import React from "react"
import GoogleMap from "./GoogleMap"

export default class WatcherInterface extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            initPos: null
        }
    }

    render() {
        return (<GoogleMap player={this.props.player} />);
    }

}


