import React from 'react'
import { Map } from 'google-maps-react'

export default class MapContainer extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            position: {
                latitude: 0,
                longitude: 0
            },
            zoom: 4
        }
    }

    getCurrentPosition() {
        return new Promise((resolve, reject) => {
            if (navigator.geolocation)
                navigator.geolocation.getCurrentPosition((position) => resolve(position))
            else reject()
        })
    }

    componentDidMount() {
        this.getCurrentPosition()
            .then((position) => {
                this.setState({ position: position.coords, zoom: 18 });
            })
            .catch((error) => this.setState({ position: { latitude: 0, longitude: 0}, zoom: 4 }))
    }

    render() {
        return (<Map google={window.google}
            apiKey={"AIzaSyDJkq57UAMGEFrPd-OsV6_NYhdV2guuJf4"}
            streetViewControl={false}
            fullscreenControl={false}
            zoom={this.state.zoom}
            center={{
                lat: this.state.position.latitude,
                lng: this.state.position.longitude
            }}>
        </Map>);
    }
}


